<template>
    <div class="tab-box">
        <div class="box-title" v-if="topMenu && topMenu.hasOwnProperty('menuList')">
            <vue-scroll :ops="ops" class="mainBorderColor">
                <div class="title-box">
                    <div class="title-item" v-for="item in topMenu.menuList"
                         @click="gotoRouter(item)"
                         v-if="!item.hasOwnProperty('hide') || item.hide===false || $route.name === item.router"
                         :class="{ 'mainFontColor':checkActive(item)}">{{ item.text }}
                    </div>
                </div>
            </vue-scroll>
        </div>
        <router-view class="box-body" :class="{'no-title':(!topMenu || !topMenu.hasOwnProperty('menuList'))}"
                     :router-data="routerData" @action="action"
                     v-if="topMenu.hasOwnProperty('fullView') && topMenu.fullView"></router-view>
        <div class="box-body" :class="{'no-title':(!topMenu || !topMenu.hasOwnProperty('menuList'))}" v-else>
            <vue-scroll>
                <router-view class="box-content"
                             :router-data="routerData" @action="action"></router-view>
            </vue-scroll>
        </div>
    </div>
</template>

<script>
    export default {
        name: "tabBoxM",
        data() {
            return {
                ops: {
                    scrollPanel: {
                        scrollingX: true,
                        scrollingY: false,
                    },
                    rail: {
                        opacity: 0,
                    },
                    bar: {
                        opacity: 0,
                    }
                }
            }
        },
        props: {
            topMenu: {
                type: Object,
                default: () => {
                    return {};
                }
            },
            routerData: {
                type: Object,
                default: () => {
                    return {};
                }
            }
        },
        methods: {
            action(data) {
                this.$emit('action', data);
            },
            checkActive(item) {
                if (item.hasOwnProperty('router') && this.$route.name !== item.router) {
                    if (!item.hasOwnProperty('checkRouter') || !this.$in_array(this.$route.name, item.checkRouter)) {
                        return false;
                    }
                }
                if (item.hasOwnProperty('params') && !this.$compareObj(item.params, this.$route.params)) {
                    return false;
                }
                return true;
            },
            //点击
            gotoRouter(item) {
                if (item && item.hasOwnProperty('router')) {
                    let params = {};
                    if (item.hasOwnProperty('params')) {
                        Object.assign(params, item.params);
                    }
                    if (item.hasOwnProperty('paramsData')) {
                        Object.assign(params, item.paramsData);
                    }
                    this.$router.push({name: item.router, params: params});
                } else if (item && typeof item.func === "function") {
                    if (item.hasOwnProperty('funcData')) {
                        item.func(item.funcData);
                    } else {
                        item.func();
                    }
                } else if (item && item.hasOwnProperty('params') || item && item.hasOwnProperty('paramsData')) {
                    let params = {};
                    if (item.hasOwnProperty('params')) {
                        Object.assign(params, item.params);
                    }
                    if (item.hasOwnProperty('paramsData')) {
                        Object.assign(params, item.paramsData);
                    }
                    this.$router.push({params: params});
                }
            }
        }
    }
</script>
<style lang="scss" scoped>
    @import '../../../assets/css/common/lib.scss';

    .tab-box {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: 100;

        .box-title {
            position: absolute;
            width: 100%;
            height: 45px;
            line-height: 45px;
            top: 0;
            left: 0;
            z-index: 100;
            overflow: hidden;
            border-bottom: 1px solid #E4E7ED;

            .title-box {
                height: 100%;
                line-height: inherit;
                white-space: nowrap;
                overflow: hidden;
                margin-top: 0;

                .title-item {
                    display: inline-block;
                    padding: 0 15px;
                    height: 45px;
                    @extend .no-select;

                    &.mainFontColor {
                        border-bottom: 3px solid;
                    }

                    &:active {
                        opacity: 0.8;
                    }
                }
            }

            /deep/ .hasHBar > .__panel {
                margin-top: 3px;

                .title-box {
                    margin-top: -3px;
                }
            }
            /deep/ .__panel{
        	    -webkit-overflow-scrolling: touch;
            }
        }

        .box-body {
            position: absolute;
            overflow: hidden;
            width: 100%;
            height: calc(100% - 45px);
            top: 45px;
            left: 0;
            z-index: 90;
            background-color: #F2F2F2;

            &.no-title {
                height: 100%;
                top: 0;
            }

            .box-content {
                width: 100%;
            }
        }
    }
</style>