<template>
    <tab-box-m :top-menu="topMenu"></tab-box-m>
</template>

<script>
    import TabBoxM from "../../components/layout/mobile/tabBoxM";
    export default {
        name: "coupon",
        components: {TabBoxM},
        computed: {
            topMenu: {
                get() {
                    let topMenu = {
                        fullView: true,
                    };
                    switch (this.$route.name) {
                        case 'couponList':
                        case 'userCardMobile':
                            topMenu.menuList = [
                                {
                                    text:this.$L('优惠券'),
                                    router: 'couponList',
                                    paramsData: {
                                        type: 'canUse'
                                    },
                                },
                                {
                                    text:this.$L('会员卡'),
                                    router:'userCardMobile',
                                },
                            ];
                            break;
                    }
                    return topMenu;
                }
            }
        },
    }
</script>

